import {ComponentProps} from 'react';
import clsx from 'clsx';
import {ArrowRightIcon} from '@heroicons/react/solid';

import {classVariants} from '~/utils/class-variants';

interface Props extends ComponentProps<'button'> {
  variant?: 'primary' | 'secondary' | 'tertiary' | 'success' | 'danger';
  width?: 'inline' | 'full' | 'icon';
  icon?: (props: ComponentProps<'svg'>) => JSX.Element;
  text?: string;
  arrow?: boolean;
}

export function Button({
  className = '',
  variant = 'primary',
  width = 'inline',
  icon,
  text,
  arrow = false,
  children,
  ...props
}: Props) {
  const Icon = icon ?? null;
  return (
    <button
      className={clsx(
        'btn',
        classVariants(variant, {
          primary: 'btn-primary',
          secondary: 'btn-secondary',
          tertiary: 'btn-tertiary',
          success: 'btn-success',
          danger: 'btn-danger',
        }),
        classVariants(width, {
          icon: 'btn-icon',
          inline: 'btn-inline',
          full: 'btn-full',
        }),
        className,
      )}
      {...props}
    >
      {Icon ? <Icon className="w-4 h-4" /> : null}
      {text ? <p>{text}</p> : children}
      {arrow ? <ArrowRightIcon className="w-4 h-4" /> : null}
    </button>
  );
}
